

.navbar-flex-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 60px;
    margin-top: 30px;
}

.settings-wrapper {
    height: 60px;
    width: 60px;
    border: 5px solid black; 
    box-shadow: 0 15px 0 black;
    background-color:  #818DFF;
}

.menu-wrapper {
    height: 60px;
    width: 60px;
    border: 5px solid black; 
    box-shadow: 0 15px 0 black;
    background-color:  #818DFF;

    display: flex;
    justify-content: center;
    align-items: center;
}

.material-symbols-outlined  {
    font-size: 2rem;

}

.material-symbols-sharp {
    font-size: 2rem;
    color: #FFF381;
    background-color: #818DFF;
    width: 100%;
    height: 100%;
}
